export default {
	data() {
		return {
			curUserid: '',
			datas: []
		}
	},
	onLoad(options) {
		uni.showLoading({
			title: '正在加载'
		})
		this.curUserid = options.id
		this.loadData()
	},
	methods: {
		async loadData() {
			const res = await this.$api.request({
				url: '/user/' + this.curUserid,
				method: 'GET'
			})
			this.datas = res.data
			uni.hideLoading()
		},
	}
}
